@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
.app {
  background: url("https://dl.dropbox.com/s/mpmu0gjtxv2x3fs/Webp.net-resizeimage%20%281%29.jpg?raw=1") no-repeat center center fixed;
  background-size: cover;
  height: 100%;
}
.app .header {
  text-align: center;
  padding-top: 50px;
}
.app .header__title {
  font-size: 48px;
  margin: 0;
  color: white;
}
.app .employees {
  margin-top: 50px;
  display: grid;
  margin: 40px;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
}

@media screen and (min-width: 992px) {
  .app .employees {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 50px;
  }
}
*{margin:0;padding:0;box-sizing:border-box}body{background:url("https://dl.dropbox.com/s/mpmu0gjtxv2x3fs/Webp.net-resizeimage%20%281%29.jpg?raw=1") repeat center center fixed;background-size:cover}.app .employees-tickets{margin-top:50px;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:20px}@media screen and (min-width: 992px){.app .employees-tickets{grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:50px}}

.card{background-color:rgba(255,255,255,.089);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);width:180px;padding:10px;text-align:center;margin:0 auto;border-radius:15px;border:2px solid rgba(189,189,189,.034);box-shadow:20px 20px 22px rgba(0,0,0,.3);color:#ffffffed;transition:.3s;display:flex;flex-direction:column;justify-content:space-around}.card:hover{box-shadow:20px 20px 22px rgba(0,0,0,.5)}.card__name{border-bottom:1px solid #ffffff91;padding-bottom:10px}.card__role{font-style:italic;opacity:.8;padding:5px}
.counter{border:2px solid rgba(255,255,255,.226);margin:10px;border-radius:15px;padding:5px;background-color:rgba(216,216,216,.404);color:rgba(0,0,0,.691)}.counter__name{font-size:24px}.counter .far{font-size:16px;padding:10px;cursor:pointer;color:rgba(0,0,0,.746)}.counter .far:hover{color:rgba(54,93,139,.342)}
